import Aurora from '../page/Aurora/index'
import Home from '../page/Aurora/Home/index'
import Article from '../page/Aurora/Article/index'


export default new VueRouter({
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'aurora',
      component: Aurora,
      children:[{
        path: '/',
        name: 'home',
        component: Home,
      },{
        path: '/article',
        name: 'article',
        component: Article,
      }]
    },
    {
      path: '/admin',
      name: 'admin',
      component: resolve => require(['../page/Hesperis/index'], resolve),
      children:[{
        path: '/admin',
        name: 'board',
        component: resolve => require(['../page/Hesperis/Board/index'], resolve),
      },{
        path: '/admin/user',
        name: 'user',
        component: resolve => require(['../page/Hesperis/User/index'], resolve),
      },{
        path: '/admin/article/add',
        name: 'add',
        component: resolve => require(['../page/Hesperis/Articles/add'], resolve),
      }]
    }
  ]
})
