<template>
    <div class="yu-hemera">
        <Snow></Snow>
        <img class="yu-hemera--img" :src="value" alt="lhlyu">
        <slot></slot>
    </div>
</template>

<script>
    import Snow from '../Snow/index'

    export default {
        name: "Hemera",
        components:{
            Snow
        },
        props:{
            img:{
                type: String,
                validator(val){},
                default:"https://cdn.jsdelivr.net/gh/lhlyu/pb@master/2019/3.jpg"
            }
        },
        data(){
            return {
                value:"https://cdn.jsdelivr.net/gh/lhlyu/pb@master/2019/3.jpg"
            }
        },
        async mounted(){
            this.loadImg()
        },
        methods:{
            loadImg(){
                if(this.img){
                    var loadimg = new Image();
                    loadimg.src = this.img;
                    let self = this
                    loadimg.onload = function(){
                        self.value = loadimg.src
                    }
                }
            }
        }
    }
</script>